import React,{useState} from 'react'
import { Outlet } from 'react-router-dom';
import { PageHeader } from 'antd'
import '@/static/pages.less'
export default function Pages() {
const [ative,setAtive]=useState('首页')

  const texts = [
    '首页',
    '产品服务',
    '客户案例',
    '关于我们'
   ]


  return (
    <div>
      <PageHeader
        title={<div style={{color:'#2340b3'}}>
           这是一个官方网站
        </div>}
        extra={
          < >
            {
              texts.map(i => <div className='header-item' key={i} style={{ color: ative === i ? '#2340b3' : '' }}
                onClick={() => {
                 setAtive(i)
               }}
              >
             {i}
            </div>)
            }
          </>
        }
      ></PageHeader>
      <Outlet/>
     </div>
  )
}

